import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			master_info: '',
			data: [],
			id:0,
			showModalStatus:false,
			yulan:''
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		if (app.globalData.isRequest == 1) {
			if (app.isAuth()) {
				this.master_info = app.globalData.master_info;
				this.detail();
			}
		} else {
			// 由于 getUserInfo 是网络请求，可能会在 Page.onLoad 之后才返回
			// 所以此处加入 callback 以防止这种情况
			app.isRequestCallback = isRequest => {
				if (isRequest == 1) {
					if (app.isAuth()) {
						this.master_info = app.globalData.master_info;
						this.detail();
					}
				}
			}
		}
	},
	methods: {
		//获取订单详情
		detail() {
			this.request({
				url: interfaces.orderQdDetail,
				data: {
					id: this.id,
				},
				header: {
					token: this.master_info.token,
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						res.data.create_time_txt = common.formatDT(res.data.create_time, 'yyyy.MM.dd h:m');
						res.data.pics = res.data.pics.split(",");	
						this.data=res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			})
		},
		//点击报修图片预览图片事件
		preImage(url) {
			uni.previewImage({
				current: url,
				urls: this.data.pics,
			})
		},

		//下载工单图片
		download(e) {
			uni.downloadFile({
				url: this.yulan,
				success: function(res) {
					//console.log(res);
					//图片保存到本地
					uni.saveImageToPhotosAlbum({
						filePath: res.tempFilePath,
						success: function(data) {
							wx.showToast({
								title: '保存成功',
								icon: 'success',
								duration: 2000
							})
						},
						fail: function(err) {
							//console.log(err);
							if (err.errMsg === "saveImageToPhotosAlbum:fail auth deny") {
								wx.showModal({
									title: '提示',
									content: '只有授权成功才能使用此功能，是否手动开启授权？',
									success: (res) => {
										if (res.confirm) {
											wx.openSetting({
												success(settingdata) {
													console.log(settingdata)
													if (settingdata.authSetting['scope.writePhotosAlbum']) {
														wx.showToast({
															title: '授权成功',
															icon: 'none',
															mask: true,
														})
													} else {
														wx.showToast({
															title: '授权失败',
															icon: 'none',
															mask: true,
														})
													}
												}
											})
										}
									}
								})
							}
						},
						complete(res) {
							console.log(res);
						}
					})
				}
			})

		},

		showModal(url) {
			this.yulan=url;
			this.showModalStatus= true;	
		},

		//隐藏对话框并保存图片
		hideModal() {
			this.download();
			this.showModalStatus= false;
		},

		//隐藏对话框
		closeModal() {
			this.showModalStatus= false;
		},

		//切换菜单栏
		changetab(tab) {
			this.tab= tab;
		},

		baoming() {
			var id = this.id;
			var token = this.master_info.token;
			var _this=this;
			wx.showModal({
				title: '提示',
				content: '确定要报名此单吗？',
				success: res => {
					if (res.confirm) {
						_this.request({
							url: interfaces.orderBaoming,
							data: {
								id: id,
							},
							header: {
								token:token
							},
							method: 'POST',
							success: res => {
								console.log(res);
								if (res.code == 200) {
									uni.showToast({
										title: res.msg,
										icon: 'none',
										mask: true,
										complete: function() {
											setTimeout(function() {
												uni.navigateBack({
													delta: 1
												})
											}, 1500);
										}
									})
								} else {
									app.isLogin(res.code, res.msg);
								}
							}
						})
					}
				}
			})
		}

	}
}
